import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FirebaseConfigurationService } from '../../services/firebase-configuration.service';

@Component({
  selector: 'app-price',
  templateUrl: './price.component.html',
  styleUrl: './price.component.scss'
})
export class PriceComponent implements OnInit {
  pricePaymentIntent: string = "";
  constructor(private router: Router, private firebaseConfigService: FirebaseConfigurationService) { }

  ngOnInit() {
    this.firebaseConfigService.getPricePaymentIntent().subscribe(price => {
      this.pricePaymentIntent = (price / 100).toFixed(2);
    });
  }

  navigateTo(route: string) {
    this.router.navigateByUrl(route);
  }
}
